.markersPopup .leaflet-popup-content-wrapper {
  border-radius: 0;
  width: 25rem;
}

.markersPopup .leaflet-popup-content {
  margin: 0;
  width: 25rem !important;
}

.markersPopup .leaflet-popup-tip-container {
  display: none;
  width: 25rem;
}

@media (max-width: 430px) {
  .markersPopup {
    bottom: -10rem !important;
    left: 0 !important;
  }
}

